.container{
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
}
.container button{
  margin: 0 10px;
  border: 0px solid #fff;
  border-radius: 10px;
  padding: 7px 10px;
  color: #fff;
  margin-top: 5px;
}
.counter{
  display: flex;
  align-items: center;
}
.btn__up{
  background-color: green;
}
.btn__down{
  background-color: red;
}
.counter span {
  font-size: 40px;
  font-weight: bold;
}